import Head from 'next/head'
import Link from 'next/link'
import getConfig from 'next/config'

import Page from '../layouts/main'


const PostLink = props => {
  return (
    <Link href={`/post?id=${ props.id }`} as={ `${ props.publicUrl }/${ props.id }` }>
      <a>{ props.title }</a>
    </Link>
  );
};


const PostCard = props => {
  let datePublishedToDsiplay = "";
  if (props.datePublished) {
    if (props.datePublished.getFullYear) {
      datePublishedToDsiplay = `${props.datePublished.getFullYear()}.${("00" + props.datePublished.getMonth() + 1).slice(-2)}.${("00" + props.datePublished.getDate()).slice(-2)}`;
    }
  }

  let datePublishedToValue = "";
  if (props.datePublished) {
    if (props.datePublished.getFullYear) {
      datePublishedToValue = `${props.datePublished.getFullYear()}-${("00" + props.datePublished.getMonth() + 1).slice(-2)}-${("00" + props.datePublished.getDate()).slice(-2)}T${("00" + props.datePublished.getHours()).slice(-2)}:${("00" + props.datePublished.getMinutes()).slice(-2)}+09:00`;
    }
  }

  return (
    <article className="post-card p1">
      <div className="left p1">
        <Link href={`/post?id=${ props.id }`} as={ `${ props.publicUrl }/${ props.id }` }>
          <img width="128" height="128" src="https://placehold.jp/128x128.png" className="thumb" />
        </Link>
      </div>
      <div className="clearfix">
        <h2>
          <Link href={`/post?id=${ props.id }`} as={ `${ props.publicUrl }/${ props.id }` }>
            <a className="post-link-text">{ props.title }</a>
          </Link>
        </h2>
        <time dateTime={ datePublishedToValue }>{ datePublishedToDsiplay }</time>
      </div>
      <style jsx>{`
        .post-card {
          border: 1px solid black;
          border-radius: .25rem;
        }
        .thumb {
          width: 96px;
          height: 96px;
          cursor: pointer;
        }
        .post-link-text {
          color: #333;
          text-decoration: none;
        }
      `}</style>
    </article>
  );
};


const Index = props => {
  const { publicRuntimeConfig, } = getConfig();

  const { siteName, siteDescription, } = publicRuntimeConfig;
  const pageTitle = `${siteName}${siteDescription ? ` \u007c ${siteDescription}` : ''}`;

  return (
    <Page isTopPage={ true }>
      <Head>
        <title>{ pageTitle }</title>
      </Head>
      <div className="p2 md-px3">
        <ul className="mt0 list-reset">
          <li className="mb2"><PostCard id='1st' title='一番目' datePublished={ "2019-01-22T16:43+09:00" } publicUrl={ publicRuntimeConfig.publicUrl } /></li>
          <li className="mb2"><PostCard id='2nd' title='二番目' datePublished={ "2019-01-20T14:21+09:00" } publicUrl={ publicRuntimeConfig.publicUrl } /></li>
          <li className="mb2"><PostCard id='3rd' title='三番目' datePublished={ "2019-01-19T09:09+09:00" } publicUrl={ publicRuntimeConfig.publicUrl } /></li>
        </ul>
        <p>NodeEnv は { process.env.NODE_ENV }</p>
        <Link href='/about' as={ `${ publicRuntimeConfig.publicUrl }/about` }><a>About</a></Link>
      </div>
      <script
        type='application/ld+json'
        dangerouslySetInnerHTML={{ __html: `
          {
            "@context": "http://schema.org/",
            "@type": "Article"
          }`
        }}
      />
    </Page>
  );
};

export default Index;